<template>
  <div class="center">
    <el-card class="box-card">
      <el-form :model="value" :rules="rules" label-width="120px" style="width: 600px" size="small">
        <el-form-item label="桌号：" prop="deskNum">
          <el-input-number v-model="value.deskNum" :min="0" controls-position="right"/>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="medium" @click="commit()">生成</el-button>
        </el-form-item>
      </el-form>

      <div  v-if="url!==''" class="image-preview">
        <el-image
          style="width: 300px; height: 300px"
          :src="url[1]"
          :preview-src-list="srcList">
        </el-image>
      </div>
    </el-card>
  </div>
</template>

<script>

  import {qrCode} from "../../api/shop";

  export default {
    data() {
      return {
        value: {
          deskNum: '',
        },
        url: '',
        srcList: [],
        //选中商品分类的值
        rules: {
          deskNum: [
            {required: true, message: '请输入店铺桌号', trigger: 'blur'},
            {min: 1, max: 10, message: '长度在 1 到 10 个数字', trigger: 'blur'}
          ],
        }
      };
    },
    methods: {
      //处理编辑逻辑
      commit() {
        qrCode(this.value.deskNum).then(response => {
          this.url=response.data;
          this.srcList.push(this.url);
          console.log(this.url)
          this.$message({
            type: 'success',
            message: '提交成功',
            duration: 1000
          });
        })
        this.$router.push({path: '/info/qrcode'});
      }
    }
  }
</script>

<style scoped>

  .box-card {
    width: 380px;
  }

  .center {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80%;
    margin-top: 50px;

  }

  .image-preview {
    height: 300px;
    width: 300px;
    padding-left: 15px;
  }
</style>



